.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.slider { 
  margin-top: 2rem;
  position: relative;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.image{
  width: 100%;
  height: 700px;
  border-radius: 10px;
}

.right-arrow{
  position: absolute;
  top: 50%;
  right: 32px;
  font-size: 3rem;
  color: black;
  z-index: 10;
  cursor: pointer;
  user-select: none;
}

.left-arrow{
  position: absolute;
  top: 50%;
  left: 32px;
  font-size: 3rem;
  color: black;
  z-index: 10;
  cursor: pointer;
  user-select: none;
}
.slide{
  opacity: 0;
  transition-duration: 1s ease;
}
.slide.active{
  opacity: 1;
  transition-duration: 1s;
  transform: scale(1.08);
}

div.pasosConsejos ul {
  list-style-type: decimal;
}

footer ul{
  list-style-type: none;
}

.quienes{
  width: 100%;
  display: flex;
  padding: 1.5rem;
}

.kien50{
  width: 40%;
  margin-left: auto;
  margin-right: auto;
}
.kien50 h3{
  color: cadetblue;
}
.responsive {
  width: 100%;
  max-width: 100%;
  height: auto;
}

div .doble{
  justify-content: center;
  width: 100%;
  display: flex;
  float: left;
  margin: 1%;
}
div.doble input{
  margin-right: 12px;
  width: 720px;
  height: 40px;
  border-radius: 4px;
  padding: 2px 2px;
}
form{
  margin: 2%;
  padding: 2%;
}
div.texto{
  display: flex;
  width: 100%;
}
textarea{
  resize: none;
  width: 85%;
  margin-left: 8%;
}
div.send{
  display: flex;
  justify-content: center;
}
div.titulo{
  display: flex;
  justify-content: center;
}
@media (max-width: 950px) {
  .quienes {
    flex-direction: column;
  }
  .kien50{
    width: 100%;
  }
}